<template>
  <el-container>
    <el-header>
      <el-menu :default-active="menu.activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="/login">登陆</el-menu-item>
        <el-menu-item index="/test">内测</el-menu-item>
        <el-menu-item index="/about">关于</el-menu-item>
        <el-menu-item index="/explain">说明</el-menu-item>
      </el-menu>
    </el-header>
    <el-main>
      <el-row :gutter="20">
        MOLE管理系统
        <el-form ref="form" :model="form" label-width="80px">
          <el-input
            v-model="form.username"
            placeholder="用户名"
            prefix-icon="el-icon-mobile-phone"
            type="text"
          ></el-input>
          <el-input
            v-model="form.password"
            placeholder="密码"
            prefix-icon="el-icon-edit"
            type="password"
          ></el-input>
          <el-button style="width: 350px" type="primary" @click="onSubmit">登陆</el-button>
        </el-form>
      </el-row>
    </el-main>
    <Footer></Footer>
  </el-container>
</template>

<script>
import axios from 'axios'
import Footer from '@/components/layout/Footer'

export default {
  data () {
    return {
      form: {
        username: '',
        password: ''
      },
      menu: {
        activeIndex: '/login',
        enableRouter: true
      }
    }
  },
  components: {
    Footer
  },
  created: function () {
    if (sessionStorage.getItem('userInfo')) {
      this.$router.push('/dashboard')
    }
  },
  methods: {
    onSubmit () {
      axios.post('/?m=login', {
        'username': this.form.username,
        'password': this.form.password
      }).then(response => {
        if (response.data.code === 0) {
          this.$message({
            message: '登陆成功..',
            type: 'success'
          })
          sessionStorage.setItem('userInfo', JSON.stringify(response.data.data))
          this.$router.push('/dashboard')
        } else {
          this.$message.error(response.data.message)
        }
      })
    },
    handleSelect (key, keyPath) {
      this.$router.push(key)
    }
  }
}
</script>

<style>
  .el-form {
    width: 350px;
    margin: 20px auto;
  }
  .el-form .el-input {
    margin-top: 10px;
  }
  .el-form button {
    margin-top: 10px;
  }
</style>



